.container {
  width: 100%;
  height: 100%;
}

.input {
  display: none;
}

.button {
  background-color: rgb(250, 219, 219);
  font-size: 0.8rem;
  font-weight: bold;
  padding: 0.5em;
  cursor: pointer;
  outline: none;
  width: 100%;
  border: none;
}

.button:hover {
  opacity: 0.8;
}
